  color: var(--wp--preset--color--white);
  text-transform: uppercase;
  text-align: center;
  display: inline-block;
  padding: 14px 20px;
  height: 100%;
}
.btn-block {
  display: block;
  width: 100%;
}
.btn-small {
  padding: 4px 30px;
}
.btn-small:after {
  display: none;
}
