.container:after,
.container-fluid:after,
.row:after {
  clear: both;
}

.embed:before, .embed:after {
  content: " ";
  display: table;
  grid-column-start: 1;
  grid-row-start: 1;
  grid-column-end: 2;
  grid-row-end: 2;
}

.embed:after {
  clear: both;
}

