  display: inline-block;
  position: relative;
  width: 127px;
  height: 60px;
}

@media (min-width: 600px) {
  .logo {
    margin: 0;
    padding: 0;
  }
  .logo:after {
    display: none;
  }
}
@media (min-width: 1025px) {
  .logo {
    margin: auto;
  }
}
.logo-base {
  position: relative;
  width: 100%;
  height: 100%;
  margin-right: auto;
  margin-left: auto;
}

.logo-needle {
  position: absolute;
  left: 0px;
  top: 0px;
  height: 100%;
  max-width: none;
}

@media screen and (max-width: 991px) {
  .logo-base {
    width: 100%;
    height: 100%;
  }
  .logo-needle {
    max-width: none;
  }
}
.logo-needle.animated {
  animation: compass_out 500ms ease-out forwards;
}

.logo-needle:hover {
  animation: compass_in 1700ms ease-out forwards;
}

@keyframes compass_in {
  0% {
    transform: rotate(0deg);
  }
  35% {
    transform: rotate(-55deg);
  }
  58% {
    transform: rotate(-35deg);
  }
  75% {
    transform: rotate(-45deg);
  }
  92% {
    transform: rotate(-40deg);
  }
  100% {
    transform: rotate(-44deg);
  }
}
@keyframes compass_out {
  from {
    transform: rotate(-44deg);
  }
  to {
    transform: rotate(0deg);
  }
}